<template>
<div class="index-2">
  <div class="point-list">
    <div class="point-01" v-for="(value,i) in pointList" :key="i" >
      <p class="title">{{i+1}}、是否有{{value.showName || value.diseaseDesc}}。</p>
      <div class="select-btn clearfix">
        <div class="select-yes fl" :class="value.required == 1?'active':''"  @click="selectTap(1,value)">
          <i></i> 是
        </div>
        <div class="select-no fr" :class="value.required == 0?'active':''" @click="selectTap(0,value)">
          <i></i> 否
        </div>
      </div>
      <div v-if="value.subDiseaseList.length>0" class="point-02 clearfix" v-show="value.required == 1">
        <span v-for="(item,n) in value.subDiseaseList" 
        :class="item.required == 1?'active':''" 
        :key="n" @click="subItemTap(item,value.subDiseaseList)">{{item.diseaseDesc}}</span>
      </div>
    </div>
  </div>
  <div class="blank"></div>
  <div class="intell-btn">
    <span @click="intellFilter">智能核保</span>
  </div>
  <app-loading :loaded="loaded"></app-loading>
</div>
</template>
<script>
  import Vue from "vue";
  import loading from "@/components/commons/loading";
  import motheds from "@/api/motheds";
  import _ from "lodash";
  import { mapMutations } from "vuex";
  import { mapGetters } from "vuex";
  export default {
    name: "Index2",
    data() {
      return {
        pointList:null,
        //加载开关
        loaded: false,
      };
    },
    created() {
      // 获取地址栏key 与code
        //  this.$route.query.key,
        // 获取地址栏key 与code
        if (this.$route.query.key && this.$route.query.proCode) {
            var data = {
            key: this.$route.query.key,
            code: this.$route.query.proCode
            };
            this.set_msg({ key: "intelligent",lev: "int_quey", data: data});
        }
    },
    methods: {
      //获取测算页带过来的fromvalue值
      getInform() {
        this.$http.getHttp({
            name: "getInfo",
            params: [{ name: "key", val: this.$route.query.key }],
            header: 2
          },res => {
            var datas = JSON.parse(res.data);
            console.log(datas);
            sessionStorage.setItem("productProcess",JSON.stringify(datas.info.productProcess))
            this.writeInformDatas = JSON.parse(res.data).info;
            this.getPointList(); //疾病Ajax请求
          }
        );
      },
      //请求疾病列表
      getPointList() {
          console.log(this.$route.query.proCode)
          this.$http.getHttp({
            name: "flagIndex",
            params: {},
            code: this.$route.query.proCode
          },
          res => {
            console.log(res)
            this.loaded = true;
            this.pointList = res.data.riskData;
             //paperId 缓存
            this.intell.paperId = res.data.paperId;
            //orderNo 缓存
            this.intell.orderNo = res.data.orderNo;
            this.set_msg({key: "intelligent",data: this.intell}); //将当前选择项缓存
          }
        );
      },
      /** 一级疾病选择*/
      selectTap(check,value){
        value.required = check;
        console.log(this.pointList)
      },
      /** 二级疾病选择*/
      subItemTap(item,value){
        let required = item.required || 0;
        //当前疾病已选择
        if(required == 1){
          //校验二级疾病选择的个数
          this.$set(item, 'required', 0)
        }else{
          this.$set(item, 'required', 1)
        }
        console.log(this.pointList)
      },
      /**疾病选择完成 请求智能核保 */
      intellFilter(){
        var vm = this;
        let isRequired = true; //是否选择患病详情校验
        let datas = {
          questions:[],
          paperId:this.intell.paperId,
          orderNo:this.intell.orderNo
        }
        // 过滤选择的疾病
        _.forEach(this.pointList,v=>{
          if(v.required == 1){
            // 二级疾病列表
            let subDiseaseList = _.filter(v.subDiseaseList, ['required', 1]) 
            if(v.diseaseCode == '99999'){
              this.intell.results.success = 'false';
              this.intell.results.resultInfo = "非常抱歉，您未能通过本次投保审核，请选择其他保险产品";
              this.set_msg({key: "intelligent",lev: "results",data: this.intell.results}); //将当前选择项缓存
              this.$router.replace({path:'/result-2'}); //跳转到问题页
              return isRequired = false;
            }
            //判断是否选择患病详情
            if(_.isEmpty(subDiseaseList)){
              vm.$popup.msg("还未选择"+v.diseaseDesc+"患病详情")
              return isRequired = false;
            } 
            _.forEach(subDiseaseList,s=>{
              let objs = {
                diseaseCode:v.diseaseCode,
                diseaseDesc:v.diseaseDesc,
              }
              objs.subDiseaseList = s;
              datas.questions.push(objs)
            })
          }
        })
        if(!isRequired)return;
        if(_.isEmpty(datas.questions))return this.$popup.msg("请先选择疾病")
        console.log(datas)
        // 请求问题或结果
        this.intellAjax(datas)
      },
      /** 请求问题或结果*/
      intellAjax(datas){
        let load = this.$popup.loading("加载中...");
        this.$http.getHttp({
          name: "answerList",
          params: datas,
          code: this.$route.query.proCode
        },
        res => {
          console.log(res)
          this.$popup.closeLay(load);
          if(res.data.questions){
            this.intell.issList = res.data.questions;
            this.set_msg({key: "intelligent",lev: "issList",data: this.intell.issList}); //将当前选择项缓存
            this.$router.push({path:'/issue-2'}); //跳转到问题页
          }else{
            this.intell.results.success = res.data.success;
            this.intell.results.resultInfo = res.data.result;
            this.set_msg({key: "intelligent",lev: "results",data: this.intell.results}); //将当前选择项缓存
            this.$router.push({ path:'/result-2'}); //跳转到结果页
          }
        });
      },
     
      ...mapMutations({
        set_msg: "SET_MESSAGE"
      })
    },
     computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        "intell"
      ])
    },
    components: {
      "app-loading": loading,
    },
    mounted() {
      this.getInform()
    }
  };
</script>

<style lang="scss">
.index-2 {
  min-height: 100%;
  background-color: #fff;
  .point-list{
    padding-top: r(30);
    .point-01{
      padding: r(20) r(30);
      .title{
        font-size: r(36);
        padding: r(10);
        font-weight: 900;
      }
      .select-btn{
        padding-top: r(30);
        padding-bottom: r(20);
        div{
          display: block;
          width: r(300);
          height: r(90);
          line-height: r(90);
          font-size: r(36);
          border: r(2) solid #dcdcdc;
          border-radius: 4px;
          color: #333;
          padding-left: r(30);
          i{
            display: inline-block;
            width: r(40);
            height: r(40);
            border-radius: 100%;
            vertical-align: middle;
            border: r(4) solid #dbdbdb;
            background-color: #fff;
            margin-right: r(10);
            position: relative;
            &::before{
              content: "";
              position: absolute;
              width: r(24);
              height: r(24);
              left: 50%;
              top: 50%;
              transform: translate(r(-12),r(-12));
              border-radius: 100%;
            }
          }
          &::before{
            content: "";
            display: inline-block;
            position: relative;
            height: 100%;
            width: 1px;
            left: 0;
            vertical-align: middle
          }
          &.active{
            background-color: rgba($color: $baseColor, $alpha: .2);
            border-color: $baseColor;
            box-shadow: 0 1px 5px rgba($color: $baseColor, $alpha: .5);
            i{
              border-color: $baseColor;
               &::before{
                 background-color: $baseColor;
               }
            }
          }
        }
      }
      .point-02{
        margin: r(10) 0;
        padding:r(20) r(10);
        border: 1px solid #cccccc;
        background-color: #efefef;
        border-radius: 5px;
        &:last-child{
          border-bottom:0
        }
        span{
          display: block;
          float: left;
          border: 1px solid #AEAEAE;
          padding: r(15) r(20);
          margin:r(10);
          border-radius: 5px;
          font-size: r(32);
          color: #333;
          background-color: #fff;
          &.active{
            border-color: $baseColor;
            color: $baseColor;
          }
        }
      }
    }
  }
  .blank{
     height: r(130);
  }
  .intell-btn{
    background-color: #fff;
    border-top: 1px solid #d2d2d2;
    padding: r(20);
    width: 100%;
    height: r(130);
    position: fixed;
    left: 0;
    bottom: 0;
    span{
      display: block;
      height: r(90);
      line-height: r(90);
      font-size: r(38);
      color: #fff;
      text-align: center;
      background-color: $baseColor;
      border-radius: 5px;
    }
  }
}
</style>


